@import '../../index.scss';
.menu {
  width: 300px;
  height: 100vh;
  background-color: $primaryColor;
  position: fixed;
  top: 0;
  right: -300px;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 1s ease-out;

  &.active {
    right: 0;
  }

  ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    font-size: 25px;
    font-weight: 100;
    color: $secondaryColor;
    width: 65%;

    li {
      margin-bottom: 25px;

      a {
        font-size: inherit;
        color: inherit;
        text-decoration: none;
      }

      &:hover {
        font-weight: 800;
        font-style: italic;
      }
    }
  }
}   